import fetch from 'utils/fetch';

//登录
export function login(phone, password) {
    const data = {
        phone,
        password,
        "login_type": 0
    };
    return fetch({
        url: '/ong/user?cmd=user_login',
        method: 'post',
        data
    });
}

//登出
export function logout() {
    return fetch({
        url: '/login/logout',
        method: 'post'
    });
}

//获取用户信息
export function getInfo(token) {
    return fetch({
        url: '/user/info',
        method: 'get',
        params: { token:"admin" }
    });
}



//注册的时候获取短信验证码
export function getCode(phone){
    return fetch({
        url:'/ong/user?cmd=get_verifyCode',
        method:'get',
        params:{
            phone:phone
        }
    });
}

//注册用户
export function regist(name,password,code,phone){
    const data = {
        name,
        password,
        code,
        phone
    }
    return fetch({
        url:'/ong/user?cmd=add_user',
        method:'post',
        data,
    });
}


